<template>
  <div class="login">
    <div class="logo">
      <div class="logoItem">
        <img src="../assets/logo.jpg" style="width:50px;height:50px" />
      </div>
      <div class="logoItem">
        <p class="title">欢迎登录</p>
      </div>
    </div>
    <nut-textinput class="my-input" v-model="name" placeholder="用户名" :clearBtnPersonnal="true">
      <template v-slot:clearBtn></template>
    </nut-textinput>
    <nut-textinput class="my-input" v-model="password" placeholder="密码" type="password" :clearBtnPersonnal="true">
      <template v-slot:clearBtn></template>
    </nut-textinput>
    <div class="btnSection">
      <button class="loginBtn" @click="toHome()">登录</button>
      <button class="registerBtn" @click="toRegister()">注册</button>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      name: null,
      password: null
    };
  },
  methods: {
    toHome() {
      if (this.name && this.password) {
        let name = window.localStorage.getItem("name");
        let password = window.localStorage.getItem("password");
        if (this.name.trim() == name && this.password == password) {
          this.$store.state.isLogin = 1;
          this.$router.push("/home/trainticket");
          this.$notify.success('登录成功');
        }else{
            this.$notify.danger('用户名或密码不正确');
        }
      } else {
             this.$notify.danger('用户名和密码不能为空');
      }
    },
    toRegister() {
      this.$router.push("/register");
    }
  }
};
</script>
<style scoped>
.title {
  font-size: 1.6em;
  font-weight: bold;
}
.logo {
  margin-top: 100px;
  margin-bottom: 20px;
}
.login {
  padding: 0 10vw;
  display: flex;
  flex-direction: column;
}
.my-input {
  margin-top: 8px;
}
.my-input1 {
  margin: 13px 0;
}
.btnSection {
  /* margin-top: 10px; */
  display: flex;
  flex-direction: column;
}
.loginBtn {
  color: #fff;
  background-color: #409eff;
  border-color: #409eff;
  line-height: 1;
  white-space: nowrap;
  cursor: pointer;
  border: 1px solid #dcdfe6;
  -webkit-appearance: none;
  text-align: center;
  outline: none;
  margin: 0;
  transition: 0.1s;
  font-weight: 500;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  padding: 12px 20px;
  font-size: 14px;
  border-radius: 4px;
  margin-top: 10px;
}
.registerBtn {
  display: inline-block;
  line-height: 1;
  white-space: nowrap;
  cursor: pointer;
  background: #fff;
  border: 1px solid #dcdfe6;
  color: #606266;
  -webkit-appearance: none;
  text-align: center;
  outline: none;
  margin: 0;
  transition: 0.1s;
  font-weight: 500;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  padding: 12px 20px;
  font-size: 14px;
  border-radius: 4px;
  margin-top: 10px;
}
.logoItem {
  text-align: center;
  margin: 8px 0;
}
</style>